"use client";

import { useState } from "react";

export default function AuthPage() {
  return (
    <div
      style={{
        width: "390px",
        height: "844px",
        borderRadius: "40px",
        background: "#FFF",
        display: "flex",
        flexDirection: "column",
        justifyContent: "flex-start",
        alignItems: "center",
        padding: "40px 20px 20px 20px",
        margin: "0 auto",
      }}
    >
      {/* Titlu cu gradient */}
      <h1
        style={{
          textAlign: "center",
          fontFamily: "Poppins, sans-serif",
          fontSize: "26.163px",
          fontStyle: "normal",
          fontWeight: 700,
          lineHeight: "148.687%",
          letterSpacing: "0.785px",
          background: "linear-gradient(180deg, #0C3057 0%, #31649A 60.1%)",
          WebkitBackgroundClip: "text",
          WebkitTextFillColor: "transparent",
          marginBottom: "20px",
        }}
      >
        Welcome to Skill Trade
      </h1>

      {/* Text explicativ */}
      <p
        style={{
          width: "277.326px",
          color: "#000",
          textAlign: "center",
          fontFamily: "Poppins, sans-serif",
          fontSize: "15.698px",
          fontStyle: "normal",
          fontWeight: 400,
          lineHeight: "normal",
          marginBottom: "20px",
        }}
      >
        Login or Sign up to access your account
      </p>

      {/* Buton Login personalizat, centrul paginii */}
      <button
        style={{
          width: "188px",
          height: "48px",
          flexShrink: 0,
          borderRadius: "0 8.721px 0 0",
          background: "#DCE9FB",
          color: "#000",
          fontFamily: "Poppins, sans-serif",
          fontSize: "19.186px",
          fontStyle: "normal",
          fontWeight: 600,
          lineHeight: "normal",
          marginBottom: "30px", // spațiu între buton și input-uri
          cursor: "pointer",
        }}
      >
        Login
      </button>

      {/* Input-uri */}
      <input
        type="text"
        placeholder="Username"
        style={{
          margin: "10px 0",
          padding: "10px",
          width: "100%",
          fontSize: "1rem",
        }}
      />
      <input
        type="password"
        placeholder="Parolă"
        style={{
          margin: "10px 0",
          padding: "10px",
          width: "100%",
          fontSize: "1rem",
        }}
      />

      {/* Buton Login clasic (opțional, îl poți șterge dacă nu-l folosești) */}
      {/* <button
        style={{
          padding: "10px 20px",
          marginTop: "10px",
          width: "100%",
          backgroundColor: "#0070f3",
          color: "white",
          border: "none",
          borderRadius: "5px",
          cursor: "pointer",
        }}
      >
        Login
      </button> */}
    </div>
  );
}
